/*
Soo, yea, this is looks like shit, I know, BUT... 
1st: no one will ever read this code,
and 2nd: I couldn't find another way to cast one type (or just a number) to another.
That's why to cast for example 1.5 to em I have to use calc(1em * 1.5) ¯\_(ツ)_/¯
*/

.card {
	--border-radius: 2;
	--border-width: 1;
	display: flex;
	gap: 2em;

	position: relative;
	
	height: 250px;
	margin-bottom: 1em;

	border: calc(1px * var(--border-width)) solid rgb(0, 0, 0);
	border-radius: calc(1em * var(--border-radius));
	overflow: hidden;
}

.info {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 1.5em 0 1.5em 0;
}

.title {
	font-family: 'Poiret One', cursive;
	font-size: 2.1em;
	font-weight: 100;
	text-shadow: 0.1px 0.1px;
	width: fit-content;
}

.icon_wrapper {
	position: absolute;
	top: 2.3em;
	right: 2em;

	display: flex;
	justify-content: space-around;
	gap: 0.8em;

	height: 1.4em;
	width: max-content;
}

.icon {
	line-height: 0 !important;
	font-size: 0 !important;
	display: contents;
	cursor: pointer;
}

.icon img {
	height: 100%;
}

.image {
	border-top-left-radius: calc((15px * var(--border-radius)) - calc(1px * var(--border-width)));
	border-bottom-left-radius: calc((15px * var(--border-radius)) - calc(1px * var(--border-width)));
	border: 4px solid #fff;
	margin: -1px;
  z-index: -1;
	box-sizing: border-box;
}

@media only screen and (max-width: 850px) {
	.card {
		height: 29.5vw;
		margin-bottom: 1.7vw;
		gap: 3.2vw;
	}

	.icon_wrapper {
		top: 3.8vw;
		right: 3.2vw;
		gap: 1.2vw;
		height: 2.3vw;
	}
	
	.info {
		font-size: 1.65vw;
	}

	.image {
		border: max(0.5vw, 3px) solid #fff;
	}
}

@media only screen and (max-width: 750px) {
	.card {
		--border-radius: 1.5;
	}
}

@media only screen and (max-width: 570px) {
	.card {
		--border-radius: 1.2;
		--border-width: 1;
	}
}

@media only screen and (max-width: 350px) {
	.card {
		--border-radius: 0.7;
		--border-width: 1;
	}
}